import dtPay from "../../common/dtpay.js";
 
export default{
	data:function(){
		return {
			pageLoad:false,
			pageData:[],
			user_address_id:0,
			coupon_id:0,
			shopid:0,
			cartid:0,
			pageHide:false,
			paytype:"alipay"
		}			
	},
	onLoad:function(ops){
		console.log(ops);
		this.cartid=ops.cartid;
		this.shopid=ops.shopid;
		this.getPage();
		
	},
	onHide:function(){
		this.pageHide=true;
	},
	onShow:function(){
		if(this.pageHide){
			this.pageHide=false;
			this.getPage();
		}
		 
	},
	methods:{
		goShop:function(shopid){
			uni.navigateTo({
				url:"../b2b_shop/index?shopid="+shopid
			})
		},
		changeAddr:function(e){				
			this.pageData.user_address_id=e.detail.value;
			var that=this;
			that.app.get({
				url:that.app.apiHost+"/module.php?m=b2b_order&a=confirm&ajax=1",
				data:{						
					shopid:that.shopid,
					user_address_id:this.pageData.user_address_id
				},
				success:function(res){						 
					that.pageData=res.data;
				}
			}) 
		},
		changePaytype:function(paytype){
			this.paytype=paytype;
		},
		setCoupon:function(cid){
			this.coupon_id=cid;
		},
		getPage:function(){
			var that=this;
			that.app.get({
				url:that.app.apiHost+"/module.php?m=b2b_order&a=confirm&ajax=1",
				data:{
					shopid:this.shopid, 
					cartid:that.cartid
				},
				success:function(res){
					if(res.error){
						uni.showToast({
							title:res.message
						})
						uni.navigateBack();
						return false;
					}else{
						that.paytype=res.data.paytype;
						that.pageLoad=true;
						that.pageData=res.data;
					}
					
				}
			})
		},
		formSubmit:function(e){
				
			var that=this;
			e.detail.value.backurl=that.app.appRoot+"#/pages/b2b_order/success";
			uni.request({
				url:that.app.apiHost+"/module.php?m=b2b_order&a=order&ajax=1&authcode="+that.app.getAuthCode(),
				data:e.detail.value,
				method:"POST",
				header:{
					"content-type":"application/x-www-form-urlencoded"
				},
				success:function(rs){
				 
					if(rs.data.error){
						uni.showToast({
							title:rs.data.message
						})
						return false;
					}
					
					if(rs.data.data.action=='pay'){
						dtPay.paytype=that.paytype;
						dtPay.urlFail="../b2b_order/fail";
						dtPay.urlSuccess="../b2b_order/success";
						dtPay.pay({
							payurl:rs.data.data.payurl,
							orderno:rs.data.data.orderno
						});						
					}
				}	
			})
		}
	}
}
